import 'document-register-element'; // IE11/FF CustomElement polyfill
import { OneWayBinding } from './one-way-binding';
import { setStyleEl, getPropsFromAttributes } from './util';

// e.g. 
// { 
//   expression: 'hero.name',
//   value: 'new value',
//   bindings: [
//     {el: <span>, type: 'innerHTML', attrName: 'foo', propName: 'hidden'}
//   ]
// },
function bindExpressions(custEl, expressions) {
  expressions.forEach(expr => {
    const func = new Function(`return this.${expr.expression};`);
    const newValue = func.bind(custEl)();

    if ( expr.value !== newValue) {
      console.log(
        `expression "${expr.expression}" value is changed to "${newValue}"`,
        `binding it to`, 
        expr.bindings.map(_ => _.el)
      );
      expr.value = newValue || '';

      expr.bindings.forEach(binding => {
        switch(binding.type) {
          case 'innerHTML':
            binding.el.innerHTML = `${newValue || ''}`;
            break;
          case 'attribute':
            binding.el.setAttribute(binding.attrName, newValue);
            break;
          case 'property':
            binding.el[binding.propName] = newValue;
            break;
        }
      })
    }
  });
}

// e.g. 
// { 
//   el: <span>,
//   bindings: [
//     {eventName: 'click', funcName: 'foo', args: ['event', 'this.x'..] }
//   ]
// },
function bindEvents(custEl, events) { // el - custom element
  events.forEach(eventBinding => {
    const bindingEl = eventBinding.el;
    eventBinding.bindings.forEach(binding => {

      const func = new Function(
        'event', 
        `return ${binding.funcName}(${binding.args.join(',')})`
      );

      bindingEl.addEventListener(binding.eventName, func.bind(custEl));
    });
  })
}


// change selectors, e.g., x7c5a,of binding.expressions and binding.events
// to DOM element =, so that users don't see weird attributes
function convertBindingSelectorsToEl(custEl, binding) {
  const selectors = [];
  binding.expressions.forEach(expr => {
    expr.bindings.forEach(binding => {
      selectors.push(binding.el);
      binding.el = custEl.querySelector(`[${binding.el}]`);
    })
  });
  binding.events.forEach(evt => {
    selectors.push(evt.el);
    evt.el = custEl.querySelector(`[${evt.el}]`);
  });
  binding.notCoverted = false;
}

// base class for all custom element
export class HTMLCustomElement2 extends HTMLElement {

  static define(tagName, klass) {
    !customElements.get(tagName) && customElements.define(tagName, klass);
  }

  disconnectedCallback() {
    this.styleEl && !(this.styleEl.numEl--) && this.styleEl.remove();
  } 

  renderWith(template, css) {
    return new Promise( resolve => {
      // some framework bind properties after DOM rendered
      // so set propertes after rendering cycle
      setTimeout(_ => {
        const props = getPropsFromAttributes(this); // user-defined attributes
        // TODO set properties using dyn. getter and setters
        // e.g. https://gist.github.com/patrickgalbraith/9538b85546b4e3841864
        for (var prop in props) { this[prop] = props[prop]; }
        if (template) {
          const tmplTarget = this.querySelector('.ce-bindings') || this;
          this.binding = new OneWayBinding(template);
          tmplTarget.innerHTML = this.binding.newHtml;;

          convertBindingSelectorsToEl(this, this.binding)
          this.detectChanges();
          bindEvents(this, this.binding.events);
        }       

        if (css) {
          this.styleEl = setStyleEl(css);  // insert <style> tag into header
          this.setAttribute(this.styleEl.id, ''); // set attribute, e.g., g9k02js84, for stying
        }

        resolve(this);
      });
    }); 
  }

  detectChanges() {
    bindExpressions(this, this.binding.expressions);
  }

}
